const getStorage = () => {
    initialState.goodsInCart = JSON.parse(sessionStorage.getItem('clientGoods')) || []
    initialState.goodsCount = JSON.parse(sessionStorage.getItem('goodsAmount')) || []
    initialState.goodsSize = JSON.parse(sessionStorage.getItem('goodsSize')) || []
    initialState.goodsPrice = JSON.parse(sessionStorage.getItem('goodsPrice')) || []
    initialState.amountOfGoodsInCart = JSON.parse(sessionStorage.getItem('amountOfGoodsInCart')) || []
}
getStorage()
initialState.totalItemsCount = initialState.merchSection.length

const goodsReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'SET_EMAIL':
            return {
                ...state,
                email: action.email
            }
        case 'SET_NAME':
            return {
                ...state,
                name: action.name
            }

        case 'SET_MESSAGE':
            return {
                ...state,
                message: action.message
            }

        case 'SET_CURRENT_PAGE':
            return {
                ...state,
                currentPage: action.page
            }
        case 'SET_GOODS_IN_CART': {
            return {
                ...state,
                goodsInCart: [...state.goodsInCart, action.goods],
            }
        }
        case 'SET_GOODS_COUNT': {
            return {
                ...state,
                goodsCount: [...state.goodsCount, action.count]
            }
        }
        case 'SET_GOODS_SIZE': {
            return {
                ...state,
                goodsSize: [...state.goodsSize, action.size]
            }
        }
        case 'SET_GOODS_PRICE': {
            return {
                ...state,
                goodsPrice: [...state.goodsPrice, action.price]
            }
        }
            case 'SET_AMOUNT_OF_GOODS_IN_CART': {
                return {
                    ...state,
                    amountOfGoodsInCart: [...state.amountOfGoodsInCart, action.count]
                }
            }
        default:
            return state;
    }
}
export const actions = {
    setUserEmail: (email) => {
        return ({type: 'SET_EMAIL', email})
    },
    setUserName: (name) => {
      return ({type: 'SET_NAME', name})
    },
    setUserMessage: (message) => {
        return ({type: 'SET_MESSAGE', message})
    },
    setNewPage: (page) => {
        return ({type: 'SET_CURRENT_PAGE', page})
    },
    setGoodsInCart: (goods) => {
        return ({type: 'SET_GOODS_IN_CART', goods})
    },
    setGoodsCount: (count) => {
        return ({type: 'SET_GOODS_COUNT', count} )
    },
    setGoodsSize: (size) => {
        return ({type: 'SET_GOODS_SIZE', size})
    },
    setGoodsPrice: (price) => {
        return ({type: 'SET_GOODS_PRICE', price})
    },
    setAmountOfGoods: (count) => {
        return ({type: 'SET_AMOUNT_OF_GOODS_IN_CART', count})
    }
}

export default goodsReducer